import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


// import { ProfilePicComponent } from './profile-pic/profile-pic.component';
import { ProfileComponent } from './profile/profile.component';
import { LandingComponent } from './landing/landing.component';
import { ContactComponent } from './contact/contact.component';
import { ProjectComponent } from './project/project.component';
import { SkillpercentComponent} from './skillpercent/skillpercent.component';
import { ProjectTimelineComponent} from './project-timeline/project-timeline.component';

const routes: Routes = [
  {
    path: '', component: LandingComponent, pathMatch: 'full', data: { animation: 'landing-page' }
  },
  {
    path: 'contact', component: ContactComponent, data: { animation: 'contact' }
  },
  {
    path: 'profiles', component: ProfileComponent, data: { animation: 'profile' }
  },
  {
    path: 'projects', component: ProjectComponent, data: { animation: 'project' }
  },
  {
    path: 'skillpercent', component: SkillpercentComponent, data: { animation: 'skillpercent' }
  },
  {
    path: 'timeline', component: ProjectTimelineComponent, data: { animation: 'projecttimeline' }
  },

  { path: '**', redirectTo: '/' },
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule {
}

