import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})
export class LandingComponent implements OnInit {

  @Output('ExploreMe')
  Contact = new EventEmitter();

  startLink1Animation = false;
  startLink2Animation = false;
  startLink3Animation = false;
  constructor(private router: Router) { }

  ngOnInit() {
    setInterval(() => {
      this.startLink1Animation = !this.startLink1Animation;
    }, 2000);
    setInterval(() => {
      this.startLink2Animation = !this.startLink2Animation;
    }, 3500);
    setInterval(() => {
      this.startLink3Animation = !this.startLink3Animation;
    }, 5000);
  }

  onContactClick() {
    this.Contact.emit();
    this.router.navigateByUrl('contact');
  }
  sendEvent = (action) => {
    (<any>window).ga('send', 'event', {
      eventCategory: action,
      eventLabel: action,
      eventAction: action,
      eventValue: 10
    });
  }

}
